import * as React from "react";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import ListItemButton from "@mui/material/ListItemButton";
import Typography from "@mui/material/Typography";

import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import GitHubIcon from '@mui/icons-material/GitHub';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

const linkOption = [
    { href: "https://github.com/Hao-Kang", icon: <GitHubIcon /> },
    { href: "mailto:haok1402@gmail.com", icon: <MailOutlineIcon /> },
    { href: "https://linkedin.com/", icon: <LinkedInIcon /> }
]

export default function SideBar(props) {
    return (
        <Drawer sx={{ width: '10%' }} PaperProps={{ sx: { width: 'inherit' } }} variant="permanent" anchor="left">
            <List>
                {props.pageOption.map((page) =>
                    <ListItemButton key={page} onClick={() => props.updatePage(page)} sx={{ paddingLeft: '10%' }}>
                        <Typography sx={{ fontWeight: page === props.page ? 'bold' : 'light' }}>
                            {page}
                        </Typography>
                    </ListItemButton>
                )}
            </List>

            <Box sx={{ display: 'flex', paddingLeft: '10%' }}>
                {linkOption.map((data) =>
                    <Link href={data.href} color={"inherit"} >
                        {data.icon}
                    </Link>
                )}
            </Box>
        </Drawer>

    );
}
